import { useMemo, useCallback, useState } from "react";

//Hooks
import useAnimationFrame from "hooks/useAnimationFrame";

import defaultStyles from './FPSStats.module.scss';


export default function FPSStats({graphWidth = 70, ...rest}) {
  const [values, setValues] = useState([]);

  const onAnimationFrame = useCallback(
    (deltaTime) => {
      //setValues
      setValues(values => {
        const keepValues = values.length > graphWidth - 1 ?
          values.slice(-(graphWidth-1))
          :
          values.slice()
        
          keepValues.push(Math.round(1/deltaTime));

          return keepValues
      })
    },
    [setValues, graphWidth]
  );

  useAnimationFrame(onAnimationFrame);

  return <DisplayStats formatAvgValue={formatFPSData} values={values} graphWidth={graphWidth} {...rest} />
}

function formatFPSData(values) {
  const curFPS = values.length === 0 ? `-` : values[values.length - 1];
  
  return `${curFPS} FPS`;
}


export function DisplayStats({formatAvgValue, values, styles = defaultStyles, graphWidth = 70, graphHeight = 29, ...rest}) {
  const graphItems = useMemo(
    () => {
      const maxValue = Math.max(...values);

      return values.map((value, index) => {
        const height = (graphHeight * value) / maxValue;

        var graphItemStyle = {
          right: (values.length -1 - index) + 'px',
          height: height + 'px',
        };

        return <div key={`item-${index}`} className={styles.graphItem} style={graphItemStyle} />;
      });
    },
    [values, styles]
  );

  const style = useMemo(
    () => ({"--graphWidth": `${graphWidth}px`, "--graphHeight": `${graphHeight}px`}),
    [graphWidth, graphHeight]
  )

  return <div className={styles.root} styles={style} {...rest}>
    {formatAvgValue(values)}
    <div className={styles.graph}>
      {graphItems}
    </div>
  </div>
}

